import {
    Button,
    Space,
    FloatingPanel,
    Popup,
    ConfigProvider,
    ErrorBlock,
    setDefaultConfig,
    FloatingBubble,
    CapsuleTabs,
    Selector,
    Toast,
} from "antd-mobile";
import {useContext, useState} from "react";
import BasePage from "./BasePage";

import Settings, {
    BlackEnum,
    ColorContext,
    ColorType,
    DaynightContext,
    SettingsContext,
    ThemeColor,
    ThemeContext
} from './Settings'

export default function MathPage({onBack}) {
    const {settings, setSettings} = useContext(SettingsContext)
    const [operator, setOperator] = useState(['+']);
    const [tab, setTab] = useState('+');
    return <BasePage
        naviTitle={''}
        onBack={onBack}
        naviRight={undefined}
    >
        <div
            style={{
                flex: 1, backgroundColor: settings.color[1],
                width: '100%',
            }}
        >
            <div
                style={{
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    padding: 15,
                    borderBottomStyle: 'solid',
                    borderBottomWidth: 1,
                    borderBottomColor: settings.black[BlackEnum.Border],
                }}
            >
                <Selector
                    style={{
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                    options={[
                        {label: '', value: '+'},
                        {label: '', value: '-'},
                        {label: '', value: ''},
                        {label: '', value: ''},
                    ]}
                    value={operator}
                    multiple={true}
                    onChange={(arr, extend) => {
                        if (arr.length != 0) {
                            setOperator(arr)
                            if (arr.indexOf('') >= 0) {
                                setTab('')
                            } else if (arr.indexOf('') >= 0) {
                                setTab('')
                            } else if (arr.indexOf('-') >= 0) {
                                setTab('-')
                            } else if (arr.indexOf('+') >= 0) {
                                setTab('+')
                            }
                        }
                    }}
                />
            </div>
            <CapsuleTabs
                activeKey={tab}
                onChange={(key) => {
                    setTab(key)
                }}
            >
                {operator.indexOf('+') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key='+'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'+'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('-') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key='-'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'-'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('+') >= 0 && operator.indexOf('-') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key='+-'
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                >
                    5
                </CapsuleTabs.Tab> : null}
                {operator.indexOf('') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key=''
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'*'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key=''
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                ><Operation1 operator={'/'}/></CapsuleTabs.Tab> : null}
                {operator.indexOf('') >= 0 && operator.indexOf('') >= 0 ? <CapsuleTabs.Tab
                    title=''
                    key=''
                    style={{
                        maxWidth: 100,
                        fontSize: Settings.getFont(1)[0],
                        lineHeight: Settings.getFont(1)[1],
                    }}
                >
                    6
                </CapsuleTabs.Tab> : null}
                {operator.indexOf('+') >= 0 && operator.indexOf('-') >= 0
                && operator.indexOf('') >= 0 && operator.indexOf('') >= 0 ?
                    <CapsuleTabs.Tab
                        title=''
                        key='+-'
                        style={{
                            maxWidth: 120,
                            fontSize: Settings.getFont(1)[0],
                            lineHeight: Settings.getFont(1)[1],
                        }}
                    >
                        7
                    </CapsuleTabs.Tab> : null}
            </CapsuleTabs>
        </div>
    </BasePage>
}

function getRan(config, isDivide = false) {
    let r = parseInt(config.base + Math.random() * config.num % config.num)
    while (isDivide && r == 0) {
        r = parseInt(config.base + Math.random() * config.num % config.num)
    }
    return r
}

function Operation1({operator}) {
    const [showSelection, setShowSelection] = useState(false);
    const {settings, setSettings} = useContext(SettingsContext)
    const [stage, setStage] = useState(0)
    const [a, setA] = useState(getRan(NumberStage[stage]));
    const [b, setB] = useState(getRan(NumberStage[stage], operator == '/'));
    const [result, setResult] = useState(parseInt(eval(a + operator + b)));
    const [answer, setAnswer] = useState(null);
    const [answerSelection, setAnswerSelection] = useState([]);
    const [correct, setCorrect] = useState(0)
    const [totalCorrect, setTotalCorrect] = useState(0)
    const [totalWrong, setTotalWrong] = useState(0)
    return <div style={{}}>
        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
                paddingLeft: 15, paddingRight: 15,
            }}
        >
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Magenta'][0][5],
                marginRight: 20,
            }}>{NumberStage[stage].title}</span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: settings.color[9],
                marginRight: 4,
            }}>
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                color: settings.color[9],
                marginRight: 4,
            }}>{correct}/
                <span style={{
                    fontSize: Settings.getFont(4)[0],
                    lineHeight: Settings.getFont(4)[1],
                    color: ThemeColor['Blue'][0][5],
                }}>10</span>
            </span>
            </span>
        </div>
        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                justifyContent: 'space-between',
                paddingLeft: 15, paddingRight: 15,
            }}
        >
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Green'][0][5],
                marginRight: 4,
            }}>
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                marginRight: 10,
            }}>{totalCorrect}</span>
            </span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Red'][0][5],
                marginRight: 4,
            }}>
            <span style={{
                fontSize: Settings.getFont(3)[0],
                lineHeight: Settings.getFont(3)[1],
                marginRight: 20,
            }}>{totalWrong}</span>
            </span>
            <span style={{
                fontSize: Settings.getFont(2)[0],
                lineHeight: Settings.getFont(2)[1],
                color: ThemeColor['Yellow'][0][5],
                marginRight: 4,
            }}>{totalWrong || totalWrong ? parseInt(totalCorrect / (totalWrong + totalCorrect) * 100) : 0}%</span>

        </div>
        <div
            style={{
                display: 'flex',
                flexDirection: 'row',
                alignItems: 'center',
                padding: 15,
                // borderBottomStyle: 'solid',
                // borderBottomWidth: 1,
            }}
        >
                <span style={{
                    fontSize: Settings.getFont(2)[0],
                    lineHeight: Settings.getFont(2)[1],
                    color: settings.color[9],
                    marginRight: 20,
                }}></span>
            <div
                style={{
                    borderRadius: 6,
                    borderStyle: 'solid',
                    borderWidth: 1,
                    borderColor: settings.black[BlackEnum.Border],
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'center',
                    minWidth: 60, padding: '0 15px',
                    height: 40,
                }}
            >
                 <span style={{
                     fontSize: Settings.getFont(2)[0],
                     lineHeight: Settings.getFont(2)[1],
                     color: settings.color[9],
                 }}>{a}</span>
            </div>
            <span style={{
                margin: '0 15px',
                fontSize: Settings.getFont(5)[0],
                lineHeight: Settings.getFont(5)[1],
                fontWeight: 'bold',
                color: settings.color[9],
            }}>{operator == '*' ? '' : operator == '/' ? '' : operator}</span>
            <div
                style={{
                    borderRadius: 6,
                    borderStyle: 'solid',
                    borderWidth: 1,
                    borderColor: settings.black[BlackEnum.Border],
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'center',
                    minWidth: 60, padding: '0 15px',
                    height: 40,
                }}
            >
                 <span style={{
                     fontSize: Settings.getFont(2)[0],
                     lineHeight: Settings.getFont(2)[1],
                     color: settings.color[9],
                 }}>{b}</span>
            </div>
            <span style={{
                margin: '0 15px',
                fontSize: Settings.getFont(5)[0],
                lineHeight: Settings.getFont(5)[1],
                fontWeight: 'bold',
                color: settings.color[9],
            }}>=</span>
            <Button
                style={{
                    borderRadius: 6,
                    // borderStyle: 'solid',
                    // borderWidth: 1,
                    // borderColor: settings.black[BlackEnum.Border],
                    // borderColor: settings.color[9],
                    display: 'flex',
                    flexDirection: 'row',
                    alignItems: 'center',
                    justifyContent: 'center',
                    minWidth: 80, padding: '0 15px',
                    height: 40,
                    backgroundColor: settings.color[9],
                }}
                onClick={() => {
                    setShowSelection(true)
                }}
            >
                 <span style={{
                     fontSize: Settings.getFont(4)[0],
                     lineHeight: Settings.getFont(4)[1],
                     color: settings.color[1],
                 }}>{answer == undefined ? '?' : answer}</span>
            </Button>
        </div>
        <Popup
            maskStyle={{
                '--background-color': settings.black[BlackEnum.Background]
            }}
            closeOnMaskClick={true}
            visible={showSelection}
            onMaskClick={() => {
                setShowSelection(false)
            }}
            onClose={() => {
                setShowSelection(false)
            }}
            bodyStyle={{
                maxHeight: '80vh',
                display: 'flex', flexDirection: 'column',
                alignItems: 'center',
                backgroundColor: settings.color[4],
                overflowY: 'auto',
            }}
        >
            <div
                style={{
                    width: '100%', height: '100%',
                    display: 'flex', flexDirection: 'column',
                    alignItems: 'center',
                }}
            >
                <span style={{
                    marginTop: 15,
                    fontSize: Settings.getFont(4)[0],
                    lineHeight: Settings.getFont(4)[1],
                    color: settings.black[BlackEnum.PrimaryText]
                }}></span>
                <div
                    style={{
                        flex: 1, width: '100%', marginTop: 15, marginBottom: 20,
                    }}
                >
                    <Selector
                        value={answerSelection}
                        style={{
                            paddingLeft: 20, paddingRight: 20,
                        }}
                        options={AllValueSelector}
                        onChange={(arr, extend) => {
                            setAnswer(arr[0])
                            setShowSelection(false)
                            if (arr[0] == result) {
                                setAnswerSelection([])
                                let tc = totalCorrect + 1
                                setTotalCorrect(tc)
                                let c = correct + 1
                                //10 
                                let s = Math.min(stage + (c >= 10 ? 1 : 0), NumberStage.length - 1)
                                setStage(s)
                                if (c >= 10) {
                                    c = 0
                                }
                                setCorrect(c)
                                let _a = getRan(NumberStage[s],);
                                let _b = getRan(NumberStage[s], operator == '/')
                                setA(_a)
                                setB(_b)
                                setResult(parseInt(eval(_a + operator + _b)))
                                setAnswer(null)
                                Toast.show('')
                            } else {
                                setAnswerSelection(arr)
                                let tw = totalWrong + 1
                                setTotalWrong(tw)
                                setCorrect(0)
                                Toast.show('')
                            }
                        }}
                    />
                </div>
            </div>
        </Popup>
    </div>
}

const NumberStage = [
    {
        base: 0,
        num: 5,
        title: '0-5',
    },
    {
        base: 5,
        num: 5,
        title: '6-10'
    },
    {
        base: 10,
        num: 5,
        title: '11-15'
    },
    {
        base: 15,
        num: 5,
        title: '16-20'
    },
]
let PlusSelector = []
for (let i = 0; i < 10 + 10; i++) {
    PlusSelector.push({
        label: i.toString(),
        value: i.toString(),
    })
}
let AllValueSelector = []
for (let i = 0; i < 10 * 10 + 1; i++) {
    AllValueSelector.push({
        label: i.toString(),
        value: i.toString(),
    })
}
